
.tagList {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    list-style-type: none;
    flex-wrap: wrap;
}
.firstCaption{
    margin-right: 20px;
}

.tag {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
}
.tag__text{

    margin-right: 25px;
}

.tag__btn{
    margin-left: -20px;
    margin-right: 20px;
}