.wrapper {
    margin: 20px 0 0 0;
    background-color: #61dafb;
    width: 400px;
    border-radius: 30px;
}

.note {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.note p {

    margin-left: 20px;

}

.note button {
    min-width: 50px;
    min-height: 50px;
    margin-left: auto;
    margin-right: 20px;
}